:root {
  --primary-color: white;
  --fst-color: rgba(19, 85, 184, 0.829);
  --fstfont-color: rgba(199, 77, 46, 0.829);
  --secfont-color: rgba(14, 70, 153, 0.829);
}

body {
  background-color: rgba(0, 30, 58, 0.774);
  overflow: hidden;
  word-break: normal;
}

/* Global use properties */
h1 {
  user-select: none;
}

span {
  user-select: none;
  font-size: 2.9rem;
  color: var(--secfont-color);
}
p {
  user-select: none;
  font-size: 1.5rem;
  color: rgba(0, 0, 0, 0.603);
}
h2 {
  user-select: none;
  color: var(--fstfont-color);
  font-size: 1.6rem;
}
h2::before {
  user-select: none;
  content: "";
  position: absolute;
  margin-top: -0.65rem;
  height: 0.14rem;
  width: 25rem;
  background-color: rgba(0, 60, 138, 0.137);
}

/* container */

.InsideContainer {
  height: 100%;
  width: 100%;
  top: 0%;
  background-color: var(--primary-color);
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Main cards container */

.CardsContainer {
  padding-top: 3.8%;
  height: 80%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

/* single card container */

.card {
  z-index: 6;
  height: 53rem;
  width: 30rem;
  border-radius: 2rem;
  background-color: rgba(138, 164, 212, 0.137);
  cursor: pointer;
  transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55) 0.1s;
}

.card:hover {
  transform: translateY(-5%);
  box-shadow: 10px 10px 2px rgba(0, 0, 0, 0.527);
}

.card .insideCard {
  height: 100%;
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-left: 1.3rem;
  padding-right: 2rem;
}

.cardInactive {
  display: none;
}

.BackgroundAnim {
  position: absolute;
  z-index: 1;
  top: 0%;
  right: 0%;
}

/* About card */

.ImgContainer {
  margin-top: 1rem;
  height: 40%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0%;
}

.ImgContainer img {
  height: 40%;
  width: 92%;
  object-fit: cover;
  border-radius: 1.5rem;
}
.myDesc {
  width: 100%;
  height: 60%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 1.5rem 2.5rem;
  color: var(--fstfont-color);
  pointer-events: none;
  word-wrap: normal;
  font-size: 1.5rem;
}

.myDesc p {
  padding-bottom: 2rem;
  padding-right: 5rem;
}
.Anim2 {
  display: none;
}

/* Mob-View */

@media screen and (max-width: 1442px) {
  .CardsContainer {
    height: 95%;
    width: 97%;
  }
}

@media screen and (max-width: 1381px) {
  .CardsContainer {
    height: 93%;
    width: 95%;
  }
  .Anim1 {
    display: none;
  }

  .Anim2 {
    display: block;
  }
}

@media screen and (max-width: 426px) {
  .Anim2 {
    display: none;
  }
  .CardsContainer {
    display: none;
  }
  .MobNav {
    position: absolute;
    transform: translateY(-19.5rem);
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    height: 55rem;
  }
  .nav {
    position: fixed;
    top: 89%;
    height: 100%;
    width: 100%;
    z-index: 8;
    overflow: hidden;
  }
  .navList {
    height: 10%;
    width: 80%;
    transform: none;
    display: flex;
    align-items: center;
    justify-content: space-around;
    list-style: none;
  }
  .navListItem {
    background-color: rgba(255, 255, 255, 0);
    height: 5rem;
    width: 10rem;
    padding: 0.5rem;
  }
  .navListItem:hover {
    background-color: rgba(180, 176, 170, 0.219);
  }
  .menuIcon {
    transform: scale(1.5);
    color: white;
  }
  .navListItem h1 {
    display: none;
  }
  .navListItem h1::before {
    display: none;
  }
  .Header {
    display: none;
  }
}
