.projectContainer {
  position: absolute;
  top: 0%;
  height: 100%;
  width: 100%;
  background-color: white;
  z-index: 4;
  animation: navigate 0.4s ease-in-out;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.heading h1 {
  color: var(--fst-color);
  font-size: 4em;
  letter-spacing: 0.5rem;
}

.projectsCont {
  height: 85%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.projectCard {
  height: 35%;
  width: 25%;
  margin-left: 3rem;
  border-radius: .8rem;
  background-color: rgba(138, 164, 212, 0.103);
  box-shadow: 1px 1px 5px rgba(128, 128, 128, 0.486);
}

.projectName h1 {
  width: 48%;
  color: var(--fst-color);
  text-align: center;
  font-size: 2.5rem;
}

.projectDescription p {
  color: gray;
  font-size: 1.4rem;
  width: 90%;
  padding-left: 2rem;
}

.projectLang h2::before {
  display: none;
}

.projectLang {
  width: 90%;
  padding-left: 2rem;
}

.projBtn Button {
  height: 4rem;
  width: 96.9%;
  margin-left: 0.7rem;
}

.projBtn h1 {
  text-transform: capitalize;
  font-size: 1.8rem;
}
