html {
  font-size: 9.8px;
}

.App {
  padding: 0%;
  margin: 0%;
  height: 100%;
  width: 100%;
}

/* Message at top */
.Header {
  height: 12rem;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  align-items: center;
  justify-items: center;
  letter-spacing: 0.5rem;
}

.Header h1 {
  font-size: 7rem;
  margin: 0%;
  color: var(--fst-color);
  user-select: none;
  z-index: 3;
}

/* Nav Active */

.menu {
  color: rgba(19, 85, 184, 0.829);
  cursor: pointer;
  transform: scale(1.5);
  z-index: 8;
}
.homeMenu {
  z-index: 8;
}

/* container */

.container {
  z-index: 7;
  position: absolute;
  top: 0%;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: navAnim 0.1s ease;
  overflow: hidden;
}

.navToggle {
  position: absolute;
  height: 100%;
  width: 93%;
  top: 2%;
  right: 0%;
  transition: 0.3s ease;
}

/* Changing rem size */

@keyframes navAnim {
  100% {
    width: 98%;
    right: 0%;
  }
  66% {
    width: 96%;
    right: 0%;
  }
  33% {
    width: 94%;
    right: 0%;
  }
  0% {
    width: 93%;
    right: 0%;
  }
}

@media screen and (max-width: 1442px) {
  html {
    font-size: 9px;
  }
}
@media screen and (max-width: 1350px) {
  html {
    font-size: 8px;
  }
}

@media screen and (max-width: 1250px) {
  html {
    font-size: 7.2px;
  }
  .navToggle {
    width: 91%;
  }
}

@media screen and (max-width: 768px) {
  html {
    font-size: 4px;
  }
}
@media screen and (max-width: 426px) {
  html {
    font-size: 7px;
  }
}
