.nav {
  position: absolute;
  top: 3%;
  height: 100vh;
  width: 6.5rem;
  z-index: 3;
}

.navList {
  height: 95%;
  display: grid;
  align-content: space-evenly;
  justify-content: center;
  list-style: none;
}

.navListItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 14rem;
  width: 9.5rem;
  cursor: pointer;
  border-radius: 1rem;
  background-color: rgba(255, 255, 255, 0.212);
  transition: .3s ease-out;
}

.navListActive {
  background-color: rgba(3, 3, 3, 0.301);
}

.navListItem:hover {
  background-color: rgba(0, 0, 0, 0.219);
}

.navListItem h1 {
  color: #ffffff;
  font-size: 1.9rem;
}

.navListItem h1::before {
  content: "";
  position: absolute;
  left: 1.5rem;
  margin-top: 5.5rem;
  height: 0.15rem;
  width: 8rem;
  background-color: #ffffff;
  pointer-events: none;
}

.menuIcon {
  cursor: pointer;
  color: #fffffff1;
  padding: 1.5rem;
  border-radius: 3rem;
}
.MobNav{
  display: none;
}