.SkillsContainer {
  position: absolute;
  top: 0%;
  height: 100%;
  width: 100%;
  display: flex;
  background-color: white;
  z-index: 4;
  animation: navigate 0.4s ease-in-out;
  overflow-y:scroll;
}

.SkillsCont {
  height: 140%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.Skillsheading {
  height: 7rem;
}

.Skillsheading h1 {
  color: var(--fst-color);
  font-size: 4em;
  letter-spacing: 0.5rem;
}

.frontEnd{
  height: 30%;
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  padding-top: 10rem;
}

.skillHeading{
  color: var(--fstfont-color);
  align-self: flex-start;
  margin-left: 10rem;
  font-size: 2.5rem;
  transform: translateY(-5rem);
}

.frontEnd .cont{
  height: 80%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.cont .skill{
  height: 65%;
  width: 19%;
  background-color: var(--fst-color);
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around; 
  align-items: flex-end;
}
.skill h1{
  color: var(--fst-color);
}

.skill .lang{
  height: 85%;
  width: 95%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 1rem;
  background-color: rgb(242, 252, 253);
  box-shadow: -1px 2px 3px rgba(0, 0, 0, 0.363);
  transform: translateX(2rem);
}

.lang .logo{
  height: 100%;
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translate(1rem,-2rem);
}

.logo svg{
  height: fit-content;
  border-radius: 2rem;
}

.lang .name{
  height: 100%;
  width: 50%;
}

.range{
  height: 10%;
  width: 90%;
  border-radius: 1rem;
  background-color: rgb(252, 252, 252);
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.384);
}
.range h1{
  transform: translate(8rem,-2rem);
  font-size: 1.5rem;
  color: var(--fstfont-color);
}

.r{
  height: 95%;
  border-radius: 2rem;
  background-color: var(--fstfont-color);
  transform: translateY(-3rem);
}

.skill Button{
  height: 3rem;
  margin-bottom: 2rem;
  width: 15rem;
}
.skill Button h1{
  font-size: 1.5rem;
  color: white;
  text-transform: capitalize;
  font-weight: 500;
} 
