.ContactCont {
  position: absolute;
  top: 0%;
  height: 100%;
  width: 100%;
  background-color: #fff;
  z-index: 4;
  animation: navigate 0.4s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.innerCont {
  height: 89%;
  width: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.contactHeading {
  height: 25%;
}

.contactHeading h1 {
  color: var(--fst-color);
  font-size: 5rem;
}

.contactHeading p {
  color: gray;
  font-size: 1.8rem;
  width: 50rem;
}

.cForm {
  height: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transform: scale(1.5);
}

.contIcon {
  color: var(--fst-color);
}

.msg {
  transform: scale(1.4) translateY(-0.6rem);
}

.ip {
  padding-top: 3rem;
}
.submitbtn {
  height: 10rem;
}
.submitbtn h1 {
  font-size: 1.5rem;
  color: var(--fst-color);
  text-transform: capitalize;
}


.mycont {
  display: flex;
  align-items: center;
}

.mycont p {
  margin-left: 2rem;
  color: rgba(128, 128, 128, 0.74);
}

.mySocials {
  height: 30%;
  width: 30%;
  margin-left: 18rem;
  margin-top: 5rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
  transform: scale(1.5);
}

.social1 {
  color: rgb(218, 111, 129);
  cursor: pointer;
}

.social2 {
  color: var(--fst-color);
  cursor: pointer;
}
