.AboutContainer {
  position: absolute;
  top: 0%;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
  z-index: 6;
  animation: navigate 0.4s ease-in-out;
}

@keyframes navigate {
  0% {
    transform: rotate(50deg);
  }
  33% {
    transform: rotate(-2deg);
  }
  66% {
    transform: rotate(2deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.AboutImg {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  user-select: none;
}
.AboutBack {
  display: none;
}

.AboutImg img {
  position: absolute;
  left: 15rem;
  height: 35rem;
  width: 35rem;
  border-radius: 50%;
  object-fit: cover;
  user-select: none;
}

@keyframes myImg {
  0% {
    transform: translateY(2rem);
  }
  33% {
    transform: translateY(-2rem);
  }
  66% {
    transform: translateY(2rem);
  }
  100% {
    transform: translateY(-2rem);
  }
}

.AboutTxt {
  letter-spacing: 0.6rem;
  width: 50%;
  height: 85%;
  user-select: none;
  padding-right: 10rem;
}

.AboutTxt h1 {
  font-size: 10rem;
  color: var(--fstfont-color);
  user-select: none;
}

.AboutTxt h3 {
  margin-top: -5rem;
  font-size: 5rem;
  color: var(--fst-color);
  user-select: none;
}

.AboutTxt p {
  font-size: 1.7rem;
  letter-spacing: 0.2rem;
  user-select: none;
}
.CV {
  height: 5rem;
  width: 20rem;
  position: absolute;
  top: 30rem;
}

.CV h2::before {
  display: none;
  text-transform: normal;
}

@media screen and (max-width: 426px) {
  @media screen and (max-width: 400px) {
    .AboutBack img {
      position: absolute;
      left: 10rem;
    }
  }
  @media screen and (max-width: 321px) {
    .AboutBack img {
      position: absolute;
      left: 6rem;
    }
  }
  .AboutImg img {
    display: none;
  }
  .AboutContainer {
    height: 95%;
    overflow-y: scroll;
  }
  .AboutImg {
    height: 100%;
    width: 100%;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  .AboutBack {
    display: block;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .AboutBack img {
    display: block;
    z-index: 1;
    height: 30rem;
    width: 30rem;
    border-radius: 50%;
  }
  .AboutTxt {
    margin-top: -4rem;
    height: 100rem;
    padding-left: 1rem;
    width: 65%;
    padding-bottom: 25rem;
  }
  .AboutTxt p {
    width: 41rem;
    font-size: 2.3rem;
  }
  .AboutTxt h1 {
    font-size: 8rem;
  }
  .AboutTxt h3 {
    font-size: 4.6rem;
  }
  .CV {
    position: relative;
    top: -7rem;
    left: 5rem;
  }
  .CV h2 {
    font-size: 2rem;
  }
}
